import * as React from 'react';
import {injectIntl, IntlShape, WrappedComponentProps} from 'react-intl';
import {dereference, GedcomData, getData} from './util/gedcom_util';
import {GedcomEntry} from 'parse-gedcom';
import {compareDates, translateDate} from './util/date_util';
import {Button} from 'semantic-ui-react';
import Linkify from 'react-linkify';
import {translateTag} from './details';
import {DateOrRange, getDate} from 'topola';

interface Props {
  gedcom: GedcomData;
  indi: string;
  eventEntries: GedcomEntry[];
}

interface Event {
  type: string;
  date: DateOrRange | undefined;
  header: JSX.Element;
  subHeader: JSX.Element | null;
  place: JSX.Element | null;
  notes: JSX.Element | null;
  sources: JSX.Element | null;
}

function eventHeader(tag: string, date: GedcomEntry | null, intl: IntlShape) {
  return (
    <div>
      <span className="ui header">{translateTag(tag)}</span>
      {date && date.data ? (
        <span className="ui subheader right floated">
          {translateDate(date.data, intl)}
        </span>
      ) : null}
    </div>
  );
}

function eventFamilyDetails(
  entry: GedcomEntry,
  indi: string,
  familyEntry: GedcomEntry,
  gedcom: GedcomData,
) {
  const spouseReference = familyEntry.tree
    .filter((familySubEntry) => ['WIFE', 'HUSB'].includes(familySubEntry.tag))
    .find((familySubEntry) => !familySubEntry.data.includes(indi));

  if (spouseReference) {
    const spouseName = dereference(
      spouseReference,
      gedcom,
      (gedcom) => gedcom.indis,
    )
      .tree.filter((subEntry) => subEntry.tag === 'NAME')
      .find(
        (subEntry) =>
          subEntry.tree.filter(
            (nameEntry) =>
              nameEntry.tag === 'TYPE' && nameEntry.data === 'married',
          ).length === 0,
      );
    if (spouseName) {
      return (
        <div className="meta">z {spouseName.data.replaceAll('/', '')}</div>
      );
    }
  }
  return null;
}

function eventPlace(entry: GedcomEntry) {
  const place = entry.tree.find((subEntry) => subEntry.tag === 'PLAC');
  if (place && place.data) {
    return <div className="description">{getData(place)}</div>;
  }
  return null;
}

function eventNotes(entry: GedcomEntry, gedcom: GedcomData) {
  const lines = entry.tree
    .filter((subentry) => ['NOTE', 'TYPE'].includes(subentry.tag))
    .map((note) => dereference(note, gedcom, (gedcom) => gedcom.other))
    .map((note) => noteDetails(note));

  return <div className="description">{joinLines(lines)}</div>;
}

function noteDetails(entry: GedcomEntry) {
  return joinLines(
    getData(entry).map((line, index) => <i key={index}>{line}</i>),
  );
}

function joinLines(lines: (JSX.Element | string)[]) {
  return (
    <>
      {lines.map((line, index) => (
        <div key={index}>
          <Linkify properties={{target: '_blank'}}>{line}</Linkify>
        </div>
      ))}
    </>
  );
}

function eventSource(entry: GedcomEntry, gedcom: GedcomData) {
  const sources: JSX.Element[] = [];
  entry.tree
    .filter((subEntry) => subEntry.tag === 'SOUR')
    .forEach((source) => sources.push(sourceDetails(source, gedcom)));

  if (sources && sources.length) {
    return (
      <span className="extra">
        rdo:{' '}
        {sources.map((source, index) => (
          <span key={index}>{source}</span>
        ))}
      </span>
    );
  }
  return null;
}

function sourceDetails(sourceReference: GedcomEntry, gedcom: GedcomData) {
  const source = dereference(sourceReference, gedcom, (gedcom) => gedcom.other);
  const sourceTitle = source.tree.find((subEntry) => subEntry.tag === 'TITL');

  if (sourceTitle && sourceTitle.data) {
    const sourcePage = sourceReference.tree.find(
      (subEntry) => subEntry.tag === 'PAGE',
    );
    if (sourcePage && sourcePage.data) {
      if (sourcePage.data.startsWith('http')) {
        return (
          <Button
            className="ui mini button"
            as="a"
            target="_blank"
            rel="noreferrer"
            href={sourcePage.data}
          >
            {sourceTitle.data}
          </Button>
        );
      }
      return (
        <>
          {sourceTitle.data} ({sourcePage.data})
        </>
      );
    }
    return <>{sourceTitle.data}</>;
  }
  return <></>;
}

function eventDetails(event: Event) {
  return (
    <div className="content">
      {event.header}
      {event.subHeader}
      {event.place}
      {event.notes}
      {event.sources}
    </div>
  );
}

function getEventDetails(
  eventEntries: GedcomEntry[],
  gedcom: GedcomData,
  indi: string,
  intl: IntlShape,
  detailsFunction: (event: Event) => JSX.Element | null,
): JSX.Element | null {
  const events = eventEntries
    .map((eventEntry) => toEvent(eventEntry, gedcom, indi, intl))
    .filter((event): event is Event => event !== null)
    .sort((event1, event2) => compareDates(event1.date, event2.date))
    .map((event) => detailsFunction(event));
  if (events && events.length) {
    return (
      <div className="ui segment divided items">
        {events.map((eventElement, index) => (
          <div className="ui item" key={index}>
            {eventElement}
          </div>
        ))}
      </div>
    );
  }
  return null;
}

function toEvent(
  entry: GedcomEntry,
  gedcom: GedcomData,
  indi: string,
  intl: IntlShape,
): Event | null {
  if (entry.tag === 'FAMS') {
    return toFamilyEvent(entry, gedcom, indi, intl);
  }
  return toIndiEvent(entry, gedcom, indi, intl);
}

function toIndiEvent(
  entry: GedcomEntry,
  gedcom: GedcomData,
  indi: string,
  intl: IntlShape,
): Event | null {
  const date = resolveDate(entry) || null;
  return {
    date: date ? getDate(date.data) : undefined,
    type: entry.tag,
    header: eventHeader(entry.tag, date, intl),
    subHeader: null,
    place: eventPlace(entry),
    notes: eventNotes(entry, gedcom),
    sources: eventSource(entry, gedcom),
  };
}

function resolveDate(entry: GedcomEntry) {
  return entry.tree.find((subEntry) => subEntry.tag === 'DATE');
}

function toFamilyEvent(
  entry: GedcomEntry,
  gedcom: GedcomData,
  indi: string,
  intl: IntlShape,
): Event | null {
  const family = dereference(entry, gedcom, (gedcom) => gedcom.fams);
  const familyMarriageEvent = family.tree.find((entry) => entry.tag === 'MARR');
  if (familyMarriageEvent) {
    const date = resolveDate(familyMarriageEvent) || null;
    return {
      date: date ? getDate(date.data) : undefined,
      type: familyMarriageEvent.tag,
      header: eventHeader(familyMarriageEvent.tag, date, intl),
      subHeader: eventFamilyDetails(familyMarriageEvent, indi, family, gedcom),
      place: eventPlace(familyMarriageEvent),
      notes: eventNotes(familyMarriageEvent, gedcom),
      sources: eventSource(familyMarriageEvent, gedcom),
    };
  }
  return null;
}

class EventsComponent extends React.Component<
  Props & WrappedComponentProps,
  {}
> {
  render() {
    return (
      <>
        {getEventDetails(
          this.props.eventEntries,
          this.props.gedcom,
          this.props.indi,
          this.props.intl,
          (event) => eventDetails(event),
        )}
      </>
    );
  }
}

export const Events = injectIntl(EventsComponent);
